.main__results__list {
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    max-width: 500px;
    margin-left: 10%;
    margin-top: 5%;
}

.main__vins__list {
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    max-width: 500px;
    height: fit-content;
    margin-top: 5%;
}

.main__list {
    margin-top: 25px;
    max-width: 500px;
}

.main__list__title {
    text-align: center;
    margin-bottom: 10px;
}

.main__list__item {
    color: black;
    margin: 15px;
}

.main__vins__list__item {
    display: inline-block;
    margin-right: 10px;
}

.main__list__vin {
    text-decoration: underline;
    color: black;
    margin: 5px 0px;
    font-size: 21px;
}

@media(max-width:767px) {
    .main__vins__list {
        margin: 5% auto;
        width: 100%;
    }

    .main__results__list {
        margin: 0 auto;
        padding: 10px;
    }
}